@import url(https://use.fontawesome.com/releases/v5.8.1/css/all.css);
@import url(https://afeld.github.io/emoji-css/emoji.css);
html {
  height: 100%;
  font-size: 62.5%;
}

:root {
  --clr-pink-light: #fd79a8;
  --clr-blue-light: #74b9ff;
  --clr-purple-dark: #6c5ce7;
  --clr-green: #00b894;
  --clr-red: #d63031;
  --clr-gray-lighter: #dfe6e9;
  --clr-gray-light: #b2bec3;
  --clr-gray-dark: #636e72;
  --clr-gray-darker: #2d3436;
  --clr-gray-darkest: #16191a;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  height: 100%;

  color: #16191a;

  color: var(--clr-gray-darkest);
  background-color: #dfe6e9;
  background-color: var(--clr-gray-lighter);

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.6rem;
  line-height: 1.4;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}

#root {
  height: 100%;
}

.JokeUI {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  background: -webkit-linear-gradient(322deg,
    var(--clr-blue-light),
    var(--clr-blue-light) 59%,
    var(--clr-gray-lighter)59%,
    var(--clr-gray-lighter)60%,
    var(--clr-pink-light) 60%,
    var(--clr-pink-light));

  background: linear-gradient(128deg,
    var(--clr-blue-light),
    var(--clr-blue-light) 59%,
    var(--clr-gray-lighter)59%,
    var(--clr-gray-lighter)60%,
    var(--clr-pink-light) 60%,
    var(--clr-pink-light));
}

.JokeUI footer {
  position: absolute;
  bottom: 0;
}
.JokeList {
  margin-right: 5%;
  padding: 1rem;
  height: 70%;
  flex: 3 1;
  z-index: 1;
  
  overflow-y: scroll;
  scrollbar-width: none;
  -ms-overflow-style: none;

  background: var(--clr-gray-lighter);
  box-shadow: 0 .5rem .8rem rgba(0,0,0,.6);
}

.JokeList::-webkit-scrollbar { 
  width: 0;
  height: 0;
}
.Joke {
  height: 6rem;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;

  border-bottom: .1rem solid var(--clr-gray-darkest);
}

.Joke:nth-of-type(even) {
  background: whitesmoke;
}

.Joke-vote-container {
  height: 100%;
  margin: 0 1rem;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
}

.Joke-vote-container .fa-thumbs-up,
.Joke-vote-container .fa-thumbs-down {
  cursor: pointer;
  text-shadow: 0 .2rem .4rem rgba(0,0,0,0.4);
  -webkit-transition: all 280ms;
  transition: all 280ms;
}

.Joke-vote-container .fa-thumbs-up:hover,
.Joke-vote-container .fa-thumbs-down:hover {
  -webkit-transform: rotate(35deg);
          transform: rotate(35deg);
}

.Joke-vote-container .fa-thumbs-up {
  color: var(--clr-green);
}

.Joke-vote-container .fa-thumbs-down {
  color: var(--clr-red);
}

.Joke-votes {
  height: 4rem;
  width: 4rem;
  margin: 0 .5rem;
  padding: .5rem 1.2rem;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;

  font-size: 1.8rem;
  background: whitesmoke;
  border-width: .1rem;
  border-style: solid;
  border-radius: 50%;
  box-shadow: 0 .3rem .9rem rgba(0,0,0,0.6);
}

.Joke-text {
  flex: 1 1;
  font-size: 1.4rem;
}

.Joke i.em {
  margin: 0 1rem;
  font-size: 2.7rem;

  border-radius: 50%;
  background-clip: border-box;
  box-shadow: 0 .3rem .7rem rgba(0,0,0,0.4);
}

.joke-emoji-animate {
  -webkit-transition: all 300ms;
  transition: all 300ms;
}

.joke-emoji-animate:hover {
  -webkit-animation: rotate-emoji 2s ease infinite forwards;
          animation: rotate-emoji 2s ease infinite forwards;
}

@-webkit-keyframes rotate-emoji {
  from, to {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  25% {
    -webkit-transform: rotate(30deg);
            transform: rotate(30deg);
  }
  50% {
    -webkit-transform: rotate(-30deg);
            transform: rotate(-30deg);
  }
}

@keyframes rotate-emoji {
  from, to {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  25% {
    -webkit-transform: rotate(30deg);
            transform: rotate(30deg);
  }
  50% {
    -webkit-transform: rotate(-30deg);
            transform: rotate(-30deg);
  }
}

@media screen and (max-width: 700px) {
  .Joke-text {
    padding: .3rem 0;
    height: 100%;
  
    flex: 1 1;
    overflow-y: scroll;
    scrollbar-width: none;
    -ms-overflow-style: none;
    font-size: 1.4rem;
  }
  
  .Joke-text::-webkit-scrollbar { 
    width: 0;
    height: 0;
  }
}
.Loader {
  margin-right: 5%;
  padding: 1rem;
  height: 70%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  flex: 3 1;
}

.Loader i {
  color: #fbd043;
  border-radius: 50%;
}
.SideBar {
  margin-left: 5%;
  height: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 11;

  flex: 1 1;

  background: var(--clr-purple-dark);
  box-shadow: 0 .5rem .8rem rgba(0,0,0,.6);
}

.SideBar > h1 {
  margin-bottom: 2rem;
  font-size: 4rem;
  font-weight: 400;
  color: var(--clr-gray-lighter);
  text-shadow: 0 .2rem .4rem rgba(0,0,0,.6);
}

.SideBar > h1 > span {
  font-weight: 700;
}

.SideBar img {
  width: 85%;
  max-width: 200px;
  object-fit: contain;
  border-radius: 50%;
  box-shadow: 0 .4rem .7rem rgba(0,0,0,.7);
}

.SideBar img:hover {
  -webkit-animation: shake-slow 5s;
          animation: shake-slow 5s;
}

.SideBar button {
  -webkit-transition: all 300ms;
  transition: all 300ms;
}

.SideBar button,
.SideBar button:focus {
  margin-top: 3rem;
  padding: 1rem 1.5rem;
  font-size: 2rem;

  color: var(--clr-gray-lighter);
  background: -webkit-linear-gradient(315deg,
    var(--clr-blue-light),
    var(--clr-blue-light) 50%,
    var(--clr-pink-light) 50%,
    var(--clr-pink-light));
  background: linear-gradient(135deg,
    var(--clr-blue-light),
    var(--clr-blue-light) 50%,
    var(--clr-pink-light) 50%,
    var(--clr-pink-light));
  border: none;
  border-radius: 5rem;
  outline: 0;
  cursor: pointer;
}

.SideBar button:hover {
  box-shadow: 0 .4rem .7rem rgba(0,0,0,.6);
  -webkit-transform: translateY(-.3rem);
          transform: translateY(-.3rem);
}

.SideBar button:focus {
  box-shadow: 0 .1rem .3rem rgba(0,0,0,.8);
  -webkit-transform: translateY(-.1rem);
          transform: translateY(-.1rem);
}

/* animation from codepen "CSS shake" by the AIM */

@-webkit-keyframes shake-slow { 
  0% { -webkit-transform: translate(0px, 0px) rotate(0deg); }
  2% { -webkit-transform: translate(-8px, 9px) rotate(1.5deg); }
